body {
}

#aap {
  background-image: url("https://i.pinimg.com/originals/09/84/b3/0984b3c49c367a8320794f7a45a8590d.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  position: absolute;
}

._jildert_achievement_left {
  height: 80px;
  width: 40px;
  background-color: rgb(99, 99, 99);
  border: 3px solid gray;
  border-top-left-radius: 83px;
  border-bottom-left-radius: 83px;
  position: absolute;
}

._jildert_achievement_left::after {
  content: "";
  height: 80px;
  width: 3px;
  left: 38px;
  position: absolute;
  background-color: rgb(99, 99, 99);
  z-index: 1;
}

._jildert_achievement_right {
  height: 80px;
  width: 40px;
  background-color: rgb(99, 99, 99);
  border: 3px solid gray;
  border-top-right-radius: 83px;
  border-bottom-right-radius: 83px;
  position: absolute;
  left: 41px;

  animation: rightpart 5s forwards 0.1s;
}

@keyframes rightpart {
  0% {
    left: 41px;
  }
  20% {
    left: 301px;
  }
  80% {
    left: 301px;
  }
  100% {
    left: 41px;
  }
}

._jildert_achievement_middle {
  background-color: rgb(99, 99, 99);
  border: 3px solid gray;
  border-right: none;
  height: 80px;
  width: 0;
  left: 41px;
  position: absolute;

  animation: middlePart 5s forwards 0.1s;
}

@keyframes middlePart {
  0% {
    width: 0;
  }
  20% {
    width: 260px;
  }
  80% {
    width: 260px;
  }
  100% {
    width: 0;
  }
}

._jildert_achievement_wrapper {
  position: fixed;
  margin: 16px;
  animation: fadeIn 5s forwards;
  transform: scale(2);
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

._jildert_achievement_image {
  width: 50px;
  height: 50px;
  top: 18px;
  left: 16px;
  z-index: 2;
  position: absolute;
  background-size: cover;
  opacity: 0;
  animation: logoAnimation 0.3s forwards 0.3s;
}

@keyframes logoAnimation {
  from {
    top: 12px;
    left: 11px;
    width: 70px;
    height: 70px;
    opacity: 0;
  }
  to {
    width: 50px;
    height: 50px;
    opacity: 1;
  }
}

@keyframes fadeText {
  0% {
    opacity: 0;
    left: 110px;
  }
  10% {
    left: 96px;
  }
  40% {
    opacity: 1;
  }
  70% {
    opacity: 1;
    left: 96px;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

._jildert_achievement_title {
  color: white;
  position: absolute;
  left: 96px;
  top: 22px;
  width: max-content;
  font-weight: bold;
  animation: fadeText 5s forwards 0.5s;
  opacity: 0;
  font-family: sans-serif;
}

._jildert_achievement_description {
  color: white;
  position: absolute;
  left: 96px;
  top: 46px;
  width: max-content;
  animation: fadeText 5s forwards 0.5s;
  opacity: 0;
  font-family: sans-serif;
}
